<template>
    <div
        :class="{correct, incorrect}"
    >
        <button
            @click="readWord"
        >▶️</button>
        <input
            v-model="answer"
            :disabled="correct"
            type="text"
            role="textbox"
            autocapitalize="off"
            autocomplete="off"
            autocorrect="off"
            spellcheck="false"
            @change="onChange"
        />
        <button
            @click="submit"
        >OK</button>
    </div>
</template>


<script>
export default {
    name: 'Answer',
    props: {
        word: {
            type: String,
            default: null,
        },
    },
    data() {
        return {
            answer: '',
            synth: window.speechSynthesis,
            submitted: false,
        };
    },
    computed: {
        correct() {
            return this.submitted && this.answer === this.word;
        },
        incorrect() {
            return this.submitted && this.answer !== this.word;
        },
    },
    methods: {
        readWord() {
            const voice = this.synth.getVoices().find((voice) => voice.lang == 'fr-FR');
            var utterThis = new SpeechSynthesisUtterance(this.word);
            utterThis.voice = voice;
            this.synth.speak(utterThis);
        },
        submit() {
            this.submitted = true;
        },
        onChange() {
            this.submitted = false;
        }
    },
}
</script>

<style>
.correct::after {
    content: '✔';
    color: green;
}

.incorrect::after {
    content: '❌';
    color: red;
}
</style>